@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid #000;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 3s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{position:absolute;height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid #000;background-color:#fff;opacity:.7}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}
.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}
.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 80px}
.custom-button{display:flex;justify-content:center;min-width:165px;width:auto;height:50px;letter-spacing:.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:#000;color:#fff;text-transform:uppercase;font-family:"Open Sans Condensed";font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:#fff;color:#000;border:1px solid #000}.custom-button.google-sign-in{background-color:#4285f4;color:#fff}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:#fff;color:#000;border:1px solid #000}.custom-button.inverted:hover{background-color:#000;color:#fff;border:none}
.collection-item{position:relative;width:22%;display:flex;flex-direction:column;height:350px;align-items:center}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{display:none;width:80%;opacity:.7;position:absolute;top:250px}.collection-item .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}.collection-item:hover .image{opacity:.8}.collection-item:hover .custom-button{display:flex;opacity:1}
.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:#fff;color:gray;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid gray;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus~.form-input-label{top:-14px;font-size:12px;color:#000}.group input[type=password]{letter-spacing:.3em}.group .form-input-label{color:gray;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}
.sign-in{width:380px;display:flex;flex-direction:column;margin:20px}.sign-in .title{margin:10px}.sign-in .buttons{display:flex;justify-content:space-between}
.sign-up{display:flex;flex-direction:column;width:380px;margin:20px}.sign-up .title{margin:10px 0}
.sign-in-sign-up{display:flex;justify-content:space-around;flex-wrap:wrap;width:80vw;margin:30px auto}
body{font-family:"Open Sans Condensed";padding:20px 60px}a{text-decoration:none;color:#000}*{box-sizing:border-box}
.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px;padding:25px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}
.cart-icon{position:relative;display:flex;align-items:center;justify-content:center;width:45px;height:45px;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}
.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}
.cart-dropdown{position:absolute;top:90px;right:40px;z-index:5;display:flex;flex-direction:column;width:240px;height:340px;padding:20px;border:1px solid #000;background-color:#fff}.cart-dropdown .cart-items{display:flex;flex-direction:column;height:240px;overflow-y:auto}.cart-dropdown button{margin-top:auto}
